@keyframes loader-rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-360deg);
  }
}

.js_loader-init {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: $brand-black;
  z-index: 9999999;
  overflow: hidden;

  &::before {
    content: "" !important;
    width: 50px;
    height: 50px;
    position: fixed;
    border: 5px solid transparent;
    border-color: $brand-danger transparent $brand-danger;
    animation: 0.8s ease-in-out 0s infinite loader-rotate;
    top: calc(50% - 25px);
    left: calc(50% - 25px);
    border-radius: 50% 0;
    z-index: 2;
  }

  &::after {
    content: "" !important;
    width: 50px;
    height: 50px;
    position: fixed;
    border: 4px solid lighten($brand-black, 5%);

    top: calc(50% - 25px);
    left: calc(50% - 25px);
    border-radius: 50%;
    z-index: 1;
  }
}
